html, body {
	height: 100%;
	padding: 0;
	margin: 0;
}

.site-container {
	display: flex;
	height: 100%;
	flex-direction: row;
}

.site-left-bar {
	display: flex;
	flex-direction: row;
	flex: 1 1 25%;

	.site-nav {
		list-style: none;

		.site-nav-item {}
	}
}

.site-content {
	display: flex;
	flex-direction: column;
	flex: 1 1 75%;
}